'use client';

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Skeleton } from '@/components/ui/skeleton';
import { useAuthContext } from '@/lib/auth-context';
import { Bell, Check, MessageCircle, Heart, UserPlus, Settings, RefreshCw } from 'lucide-react';
import { useState, useEffect } from 'react';
import { 
  fetchNotifications, 
  markAllNotificationsAsRead, 
  markNotificationAsRead,
  type Notification 
} from '@/lib/api/notifications';
import { formatRelativeTime, truncateText } from '@/lib/utils';

export default function NotificationsPage() {
  const { user, token } = useAuthContext();
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [loading, setLoading] = useState(true);
  const [markingAllRead, setMarkingAllRead] = useState(false);
  const [refreshing, setRefreshing] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const loadNotifications = async () => {
    if (!token) return;

    try {
      setError(null);
      const data = await fetchNotifications(token);
      setNotifications(data);
    } catch (err) {
      console.error('Failed to fetch notifications:', err);
      setError('Failed to load notifications');
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  useEffect(() => {
    if (user && token) {
      loadNotifications();
    } else {
      setLoading(false);
    }
  }, [user, token]);

  const handleMarkAllRead = async () => {
    if (!token || markingAllRead) return;

    try {
      setMarkingAllRead(true);
      await markAllNotificationsAsRead(token);
      // Update local state
      setNotifications(prev => prev.map(n => ({ ...n, read: true })));
    } catch (err) {
      console.error('Failed to mark all notifications as read:', err);
    } finally {
      setMarkingAllRead(false);
    }
  };

  const handleMarkAsRead = async (notificationId: string) => {
    if (!token) return;

    try {
      await markNotificationAsRead(notificationId, token);
      // Update local state
      setNotifications(prev => prev.map(n => 
        n.id === notificationId ? { ...n, read: true } : n
      ));
    } catch (err) {
      console.error('Failed to mark notification as read:', err);
    }
  };

  const handleRefresh = async () => {
    if (!token || refreshing) return;
    setRefreshing(true);
    await loadNotifications();
  };

  const getNotificationIcon = (type: string) => {
    switch (type) {
      case 'follow':
        return UserPlus;
      case 'new_post':
        return MessageCircle;
      default:
        return Bell;
    }
  };

  const formatNotificationText = (notification: Notification) => {
    const actorName = notification.actor.displayName || 'Someone';
    
    switch (notification.type) {
      case 'follow':
        return `${actorName} started following you`;
      case 'new_post':
        const postPreview = notification.post ? 
          truncateText(notification.post.text, 50) : 'something new';
        return `${actorName} posted: "${postPreview}"`;
      default:
        return 'New notification';
    }
  };

  if (!user) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Card className="max-w-2xl mx-auto">
          <CardContent className="text-center p-8">
            <p className="text-muted-foreground">Please log in to view your notifications.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Card className="max-w-2xl mx-auto">
          <CardContent className="text-center p-8">
            <p className="text-destructive mb-4">{error}</p>
            <Button onClick={handleRefresh} variant="outline" disabled={refreshing}>
              <RefreshCw className={`h-4 w-4 mr-2 ${refreshing ? 'animate-spin' : ''}`} />
              {refreshing ? 'Retrying...' : 'Retry'}
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }
  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">Notifications</h1>
            <p className="text-muted-foreground">Stay up to date with your activity</p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm">
              <Check className="h-4 w-4 mr-2" />
              Mark all read
            </Button>
            <Button variant="outline" size="sm">
              <Settings className="h-4 w-4 mr-2" />
              Settings
            </Button>
          </div>
        </div>

        {/* Notifications List */}
        <div className="space-y-4">
          {mockNotifications.map((notification) => {
            const IconComponent = notification.icon;
            return (
              <Card key={notification.id} className={`transition-colors hover:bg-accent/50 ${!notification.read ? 'border-primary/20 bg-primary/5' : ''}`}>
                <CardContent className="p-4">
                  <div className="flex items-start gap-4">
                    <div className={`p-2 rounded-full ${!notification.read ? 'bg-primary text-primary-foreground' : 'bg-muted text-muted-foreground'}`}>
                      <IconComponent className="h-4 w-4" />
                    </div>
                    <div className="flex-1 space-y-1">
                      <div className="flex items-center justify-between">
                        <p className="font-medium">{notification.title}</p>
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-muted-foreground">{notification.time}</span>
                          {!notification.read && <Badge variant="secondary" className="h-2 w-2 p-0" />}
                        </div>
                      </div>
                      {notification.description && (
                        <p className="text-sm text-muted-foreground">{notification.description}</p>
                      )}
                    </div>
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>

        {/* Empty State (when no notifications) */}
        {mockNotifications.length === 0 && (
          <Card>
            <CardContent className="text-center py-12">
              <Bell className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
              <CardTitle className="mb-2">No notifications</CardTitle>
              <CardDescription>
                You're all caught up! When you have new activity, it'll appear here.
              </CardDescription>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}